import React from "react";
import "./App.css";

const code = `
() => {
    const [count, setCount] = React.useState(0);
    return (
        <>
            <p>Count: {count}</p>
            <button onClick={() => setCount(count + 1)}>+1</button>
            <button onClick={() => setCount(count - 1)}>-1</button>
        </>
    )
}
`;

function App() {
  const [showText, setShowText] = React.useState("");

  return (
    <>
      {showText && (
        <div class="floating-text-div">
          <span>Hello, from speak to me!</span>
        </div>
      )}

      <div onClick={() => setShowText(!showText)} class="floating-div">
        <span>Speak To Me</span>
      </div>
    </>
  );
}

export default App;
